import React,{Component} from 'react';
import {motion} from 'framer-motion';
import {pageTransitionProp,pageVariantsProp} from '../../shared/Utility';
import classes from './Introduction.module.css';
import manSvg from '../../assets/svg/man.svg';
import ReactTypingEffect from 'react-typing-effect';

class Introduction extends Component{
    introduction={
        summary:'Dedicated and result oriented software developer with 6+ years of experience'+
        ' in front end and backend development. Driven and self-motivated, and therefore effective'+
        ' at completing task independently. Skilled in all phases of software development lifecycle, '+
        'including requirement analysis, Solution design, Development, testing and deployment.'+
        ' I understand the importance of creating highly readable and easily maintainable source code. '+
        'I am constantly striving to learn new technologies and look to ways to better myself in this'+
        ' rapidly changing industry.'
    }

    render(){


        const isMobile = window.innerWidth < 850;
        const whoAmI= (<div className={classes.whoAmI}>
                        <div className={classes.title}>Who Am I ?</div>
                        <p>{this.introduction.summary}</p>
                        </div>);

        const whoAmIMobile= isMobile? whoAmI:null;
        const whoAmIDesktop= !isMobile? whoAmI:null;            

        return (
            <motion.div initial="initial" animate="in" exit="out" variants={pageVariantsProp}
            transition={pageTransitionProp}>
  
              <div  className={classes.introductionWrapper}>
                
                <div  className={classes.gridItems}>
                    <img src={manSvg} alt="man"></img>
                </div>

                <div  className={classes.gridItems}>
                     <div  className={classes.introduction}>
                            <div  className={classes.info}>
                                <div  className={classes.name}> Gaurav Kumar </div>
                                <div></div>
                                <ReactTypingEffect  className={classes.desg}
                                typingDelay='0'
                                text='Software Developer At Infosys'>

                                </ReactTypingEffect>
                            </div>

                            <div  className={classes.info}>
                                <div  className={classes.contact}> 
                                     <b> Email:</b> Gaurav.k20192@gmail.com <br></br>
                                     <b> Ph No:</b> +46 721419461<br></br>
                                     <b> LinkedIn:</b> linkedin.com/in/gaurav-kumar-97a48667 <br></br>
                                </div>
                                
                            </div>

                           

                     </div>
                     {whoAmIDesktop}
                   

                </div>
             
                 {whoAmIMobile}
                

              </div>
                
            </motion.div>


        );
    }
}

export default Introduction;